/* eslint-disable import/prefer-default-export */
import styled, { css } from 'styled-components';
import { codeTheme } from '@ali/4ever-utils';
import { MAX_HEIGHT } from '../../utils/constants/dimension';

interface WrapperProps {
  zoom: number;
  scale: number;
  className: string;
  ref: React.RefObject<HTMLDivElement>;
  readOnly?: boolean;
  // theme
  theme: string;
  // 生成长图/pdf 模式下，展开全部代码块内容
  print?: boolean;
}

const defaultBgColor = '#f5f6f7';
const darkBgColor = '#282a36';

export const Wrapper = styled.div<WrapperProps>`
  display: flex;
  flex-direction: column;
  outline: none;
  line-height: ${codeTheme['line-height']};
  /* 为避免粘贴大段代码时，从弹性变为 fixed 导致的页面跳动问题，
      外层容器需要 overflow：hidden；该属性不能放在 resizer 上，会导致 handle 被隐藏 */
  ${props => !props.print && css`
    max-height: ${MAX_HEIGHT}px;
  `}
  /* 避免闪烁 */
  ${props => `
    background-color: ${props.theme === 'default' ? defaultBgColor : darkBgColor};
  `}
  /* 这个样式会导致 zoom hack 时，chrome 下字体出现模糊 */
  /*overflow-y: hidden;*/
  height: 100%;
  border-radius: 8px;

  /**
    https://stackoverflow.com/a/31831776/1648392
    cangjie content 指定了 user-select none， 在 safari 下无法输入
  */
  user-select: auto;

  &, & * {
    box-sizing: border-box;
  }

  .CodeMirror {
    height: 100%;
    ${props => `
      background-color: ${props.theme === 'default' ? defaultBgColor : darkBgColor};
    `}
    border-radius: 8px;
    z-index: 0;
    padding: 1px;
    /* 覆盖自定义主题、仓颉 laser 插件对 border 的覆盖 */
    border: 1px solid transparent !important;
  }

  .CodeMirror pre.CodeMirror-line, .CodeMirror pre.CodeMirror-line-like {
    font-size: 1em;
    font-family: "SFMono-Regular",Consolas,"Liberation Mono",Menlo,Courier,monospace !important;
  }

  .CodeMirror .collab-selection {
    background-color: rgba(243, 155, 53, .7) !important; 
    color: white;
  }

  .CodeMirror .find-and-replace {
    background-color: #FFE62E !important; 
  }

  .CodeMirror .CodeMirror-sizer {
    box-sizing: content-box;
  }

  .CodeMirror .find-and-replace-selected {
    background-color: #FF9933!important; 
  }

  .CodeMirror.cm-s-default {
    color: rgba(17, 31, 44, 0.56);
  }

  .CodeMirror.cm-s-default .CodeMirror-gutters {
    border-right: none;
    background-color: ${defaultBgColor};
  }

  .CodeMirror .CodeMirror-scroll {
    ${props => !props.print && css`
      max-height: ${MAX_HEIGHT}px;
    `}
  }

  // zoom 时会有双滚动条，移除掉
  .CodeMirror-scroll::-webkit-scrollbar {
    display: none;
  }

  .CodeMirror-overlayscroll-horizontal,
  .CodeMirror-overlayscroll-vertical {
    background: transparent;
  }

  .CodeMirror-overlayscroll-horizontal div,
  .CodeMirror-overlayscroll-vertical div {
    background: #d9dbdd;
    cursor: auto;
  }
  .CodeMirror-overlayscroll-horizontal div:hover,
  .CodeMirror-overlayscroll-vertical div:hover {
    background: #c6c9cd;
  }

  .CodeMirror.cm-s-dracula {
    .CodeMirror-overlayscroll-horizontal div,
    .CodeMirror-overlayscroll-vertical div {
      background: #70737a;
    }
    .CodeMirror-scrollbar-filler {
      background-color: inherit;
    }
  }

  .CodeMirror-linenumber {
    user-select: none;
  }

  .CodeMirror-cursor {
    border-width: 2px !important;
  }

  .CodeMirror-cursors {
    position: relative;
    left: -1px;
    ${({ readOnly }) => (readOnly ? 'display: none;' : '')}
  }
`;
